body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,
dl,
dt,
dd,
ul,
ol,
li,
pre,
form,
fieldset,
legend,
button,
input,
textarea,
th,
td,
form {
  margin: 0;
  padding: 0;
}

body,
button,
input,
select,
textarea {
  font: 12px/1.5 '\5FAE\8F6F\96C5\9ED1', '\5b8b\4f53', tahoma, arial;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
}

a:hover {
  color: #08c;
}

.index-mid-box {
  width: 1000px;
  height: 780px;
  position: absolute;
  margin-left: -500px;
  top: 0;
  left: 50%;
}

.index-mid-box .float-mid {
  position: absolute;
  left: 50%;
  top: 42%;
  margin-left: -270px;
  margin-top: -112px;
}

.search-box .search-btn {
  width: 94px;
  height: 45px;
  border: 0;
  /* background: url(../images/search-button.png) center no-repeat; */
  cursor: pointer;
  float: left;
}

.search-topic {
  position: absolute;
  color: #A1A1A1;
  z-index: 90;
  font-size: 14px;
  top: 50%;
  margin-top: -10px;
  line-height: 22px;
  display: block;
  width: 300px;
}

.search-box .search-text {
  padding: 10px 0 6px;
  text-indent: 10px;
  width: 460px;
  float: left;
  border: 0;
  font-size: 14px;
  position: relative;
  top: 4px;
  outline: none;
}

.table-list input {
  vertical-align: middle;
}

.info-from-box dd .input-style {
  border: 1px solid #ABADB3;
  padding: 5px 0;
  text-decoration: 5px;
}

.info-from-box dd textarea.input-style {
  height: 100px;
  overflow: auto;
}

.info-from-box dd input.input-style {
  text-indent: 5px;
}

.resume-body input.gray-button:disabled {
  border: 1px solid #ccc;
  color: #666;
  background-color: #999;
  background-image: none;
}

.hot-text a {
  color: #666;
  padding-right: 10px;
}

.hot-text,
.hot-text a {
  color: rgba(255, 255, 255, 0.80);
}

.hot-text {
  padding: 10px 0;
  color: #666;
}
