.home-main {
    position: relative;
    height: 900px;
    width: 100%;
    overflow: hidden;
    font-family: PingFangSC-Regular;
}

.index-image {
    vertical-align: middle;
    width: 100%;
}

.hot-text {
    padding: 10px 0;
    color: #666;
}

.search-box {
    width: 554px;
    height: 45px;
    background: #fff;
    border: 2px solid #F27B04;
    background: #fff;
}

form {
    display: block;
}

.search-topic {
    color: rgba(31, 56, 88, 0.30);
    line-height: 20px;
    margin-left: 8px;
}
.company-popularize .marb20{
    margin-bottom: 20px !important;
}
.fotter{
    width: 100%;
    height: 44px;
    background-color: #2c2f33;
}
.fotter-content{
    width: 1000px;
    margin: 0 auto;
    line-height: 44px;
    color: #fff;
    opacity: 0.4;
}